html {
  scroll-behavior: smooth;
}

* {
  margin: 0;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
}

.section {
  background-color: #ae1332;
  margin: 0;
  padding: 0 32px;
}

body {
  color: #fff;
  background-color: #ae1332;
  font-family: DM Sans, sans-serif;
}

.list {
  padding: 0;
  list-style: none;
}

.link, a {
  text-decoration: none;
}

.header {
  width: 100%;
  background-color: #fff;
  padding: 16px;
}

.header-container {
  max-width: 1280px;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.header-navbar-menu {
  text-align: left;
}

.box {
  justify-content: center;
  align-items: center;
  display: flex;
}

.nav-list {
  letter-spacing: -.04em;
  text-align: center;
  align-items: center;
  gap: 30px;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.28571;
  text-decoration: none;
  list-style: none;
  display: flex;
  position: relative;
}

.nav-list-item {
  color: #ae1332;
  position: relative;
}

.nav-li {
  color: #ae1332;
}

.nav-list-item:after {
  content: "";
  width: 100%;
  height: 2px;
  background-color: #a4cb66;
  transition: transform .25s ease-out;
  display: flex;
  position: absolute;
  transform: scale(0);
}

.nav-list-item:hover:after {
  visibility: visible;
  transform-origin: 0 100%;
  transform: scaleX(1);
}

.nav-list-item:hover {
  color: #a4cb66;
}

.logo, .logo-div {
  align-items: center;
  gap: 6px;
  display: flex;
}

.logo-name {
  letter-spacing: -.04em;
  text-transform: uppercase;
  text-align: center;
  color: #ae1332;
  justify-content: center;
  align-items: center;
  gap: 8px;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.28571;
  display: flex;
}

.logo-name:hover, .logo-name:focus {
  color: #a4cb66;
}

.logo-name:hover .icons, .logo-name:focus .icons, .svghover {
  fill: #a4cb66;
}

.header-logo-h-menu {
  justify-content: space-between;
  display: flex;
}

.butoon-h-menu {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
}

.butoon-h-menu-x {
  cursor: pointer;
  color: #ae1332;
  width: 24px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
}

.icons {
  fill: #ae1332;
}

.icons:hover, .icons:focus {
  fill: #a4cb66;
}

@media screen and (min-width: 768px) {
  .header-logo-h-menu, .icon-h-menu, .butoon-h-menu-x {
    display: none;
  }
}

@media screen and (max-width: 767.999px) {
  .logo, .header-container {
    display: none;
  }
}

